@import "../helpers/variables";

main section.timeline {
  display: flex;
  justify-content: center;
  height: 100vh;
  img {
    position: fixed;
    top: 0;
  }

  section {
    width: fit-content;
    section:first-of-type {
      width: 10em;
      height: 30em;
      transform: translate(0, 0);
      z-index: 1;
    }
    section:nth-of-type(2) {
      height: 10em;
      width: 50em;
      transform: translate(0, -160px);
    }
    section:nth-of-type(3) {
      width: 10em;
      height: 40em;
      transform: translate(640px, -320px);
      z-index: 1;
    }
    section:nth-of-type(4) {
      height: 10em;
      width: 40em;
      transform: translate(160px, -480px);
    }
    section:nth-of-type(5) {
      width: 10em;
      height: 30em;
      transform: translate(160px, -640px);
    }
    section:nth-of-type(6) {
      height: 10em;
      width: 30em;
      transform: translate(160px, -800px);
    }
    section:nth-of-type(7) {
      width: 10em;
      height: 40em;
      transform: translate(480px, -960px);
      z-index: 1;
    }
    section:nth-of-type(8) {
      height: 10em;
      width: 40em;
      transform: translate(0, -1120px);
    }
    section:nth-of-type(9) {
      width: 10em;
      height: 30em;
      transform: translate(0, -1280px);
    }

    section {
      position: relative;
      background: $vent;
      box-shadow: inset 0 0 15px #000000;
      display: flex;
      justify-content: center;
      article {
        position: absolute;
        bottom: 0;
        height: 10em;
        width: 10em;
        background: $vent-entrance;
        box-shadow: inset 0 0 15px #212121;
      }
      h3 {
        position: absolute;
        margin: 0;
        bottom: -1em;
      }
    }
  }
}
